  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  text-decoration: none;
}

body {
  background-color: transparent;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  margin: 0;
  text-shadow: 0.4px 0.2 black;
  background-color: rgba(150, 150, 150, 0.2);
}

a, img {
  color: #fbfaf8;
  transition: all 0.5;
}
a:hover, img:hover {
  opacity: 0.7;
  cursor: pointer;
  transform: scale(1.1);
  color: black !important;
}
a:active, img:active {
  transform: scale(1.3);
  color: #a7b7bd;
}
a:focus, img:focus {
  outline: 0;
  color: #a7b7bd;
}

nav {
  display: flex;
  flex-direction: column;
}

.fstHead {
  display: none;
}

#fstHead {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
  gap: 1rem;
}

#sndHead {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
  gap: 0.2rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  background: #a7b7bd;
  height: 3.2rem;
}

#menuBtn {
  flex: 0.2;
}

#logo {
  flex: 2;
  height: 2.8rem;
}

#labels {
  display: none;
}

.page1 {
  display: flex;
  flex-direction: column;
  margin: 3rem 0.15rem 2rem 0.15rem;
  padding: 0.5rem;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: flex-start;
}
.hero h1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100% 100%;
  background-repeat: no-repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}
.hero .fstPageP {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  border: 3px solid #a7b7bd;
  padding: 0.3rem;
  font-size: 0.9rem;
  text-shadow: 0.4px 0.2 black;
}
.hero h2 {
  margin-top: 1rem;
  font-size: 1.1rem;
}

.page2 {
  display: flex;
  flex-direction: column;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-blend-mode: color-burn;
  background-repeat: no-repeat;
}

.Page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 1.5rem;
  gap: 2rem;
  color: #a7b7bd;
}

.fstPart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.fstPart h2 {
  margin-bottom: 0.4rem;
}

.programs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.programs li {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  background-color: rgba(150, 150, 150, 0.2);
  stroke-opacity: 1;
  padding: 0.2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  max-width: 550px;
}
.programs li img {
  flex: 0.4;
}
.programs li h3 {
  flex: 0.4;
}
.programs li p {
  flex: 1;
  text-align: left;
}

.endPart {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  background-color: rgba(87, 85, 85, 0.7);
  color: #a7b7bd;
  white-space: nowrap;
  padding: 1rem;
}

@media (min-width: 375px) and (max-width: 500px) {
