    font-size: 0.8rem;
  }
  #labels #borderLabel {
    color: #a7b7bd;
    border: 3px solid #a7b7bd;
    white-space: nowrap;
    padding: 0.3rem;
  }
  .page1 {
    margin-top: 4rem;
    padding-right: calc(25% - 120px);
