    font-size: 1.5rem;
  }
  .hero h2 {
    font-size: 2.2rem;
  }
  .hero .adress {
    font-size: 1.5rem;
  }
  .programs {
    display: flex;
    flex-direction: row;
  }
  .programs li {
    display: flex;
    flex-direction: column;
    height: 12rem;
    gap: 0.5rem;
  }
  .programs li p {
    text-align: center;
  }
